<template>
  <div class="fiction">
    <van-nav-bar title="小说" />
    <div class="fiction" ref="fiction">
      <div class="fiction-item clearfix" ref="vidosbox">
        <div
          class="ping-box"
          ref="pingitem"
          v-for="(item,index) in fictionData.data.list"
          :key="index"
          @click="datailsGo({name: 'details', params: {url: item.url}})"
        >
          <div class="ping clearfix">
            <div class="phoImg fl">
              <div class="pho">
                <van-image :src="item.cover">
                  <template v-slot:error>
                    <img class="auto-img" src="../../assets/image/404.jpg" alt />
                  </template>
                </van-image>
              </div>
            </div>
            <div class="infor fl">
              <h3 class="one-text">{{item.name}}</h3>
              <span class="one-text fl">{{item.tag}}</span>
              <span class="one-text fl">{{item.num}}</span>
              <p class="three-text">{{item.introduce}}</p>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { createNamespacedHelpers } from "vuex";

const { mapState } = createNamespacedHelpers("fictionModule");

export default {
  name: "fiction",

  computed: {
    ...mapState(["fictionData", "isInit", "isHas"])
  },

  data() {
    return {
      activeName: "a"
    };
  },

  methods: {
    getcartoonsData() {
      let self = this;

      this.$toast.loading({
        duration: 0,
        message: "加载中..."
      });

      this.axios({
        // 请求参数
        methods: "GET",
        url: "http://api.pingcc.cn/?xsname=1"
      })
        .then(result => {
          // console.log("result ==>> ", result);

          if (this.isInit) {
            let vidosbox = this.$refs.vidosbox;
            // console.log("vidosbox ==> ", vidosbox);

            let vidosboxH = vidosbox.clientHeight;
            // console.log("vidosboxH ==> ", vidosboxH);

            let vidosboxP = parseFloat(getComputedStyle(vidosbox).paddingTop);
            // console.log("vidosboxP ==> ", vidosboxP);
            let timers = [];

            vidosbox.onscroll = function() {
              if (!self.isHas) {
                // console.log("没更多数据可加载");
                return;
              }

              let that = this;

              let timer = setTimeout(function() {
                // console.log("a");
                for (let i = 1; i < timers.length; i++) {
                  clearTimeout(timers[i]);
                }

                let pingitem =
                  self.$refs.pingitem[self.$refs.pingitem.length - 1];
                // console.log("pingitem ==> ", pingitem);

                let pingitemH = pingitem.clientHeight;
                // console.log("pingitemH ==> ", pingitemH);

                let pingitemP = parseFloat(
                  getComputedStyle(pingitem).paddingTop
                );
                // console.log("pingitemP ==> " pingitemP);

                let pingitemoffsetTop = pingitem.offsetTop;
                // console.log("pingitemoffsetTop ==>> ", pingitemoffsetTop);

                if (
                  that.scrollTop +
                    vidosboxH -
                    vidosboxP -
                    pingitemP * 4 -
                    pingitemH +
                    80 >=
                  pingitemoffsetTop
                ) {
                  // console.log("发起数据请求");
                  // 发起数据请求
                  self.getcartoonsData();
                }
                timers = [];
              }, 1000);

              timers.push(timer);
              // console.log("timer ==>> ", timer);
              // console.log("timers ==>> ", timers);
            };
          }
          this.$store.commit("fictionModule/changecartoons", result);
          this.$toast.clear();
        })
        .catch(err => {
          // console.log("err ==>> ", err);
          this.$toast.clear();
        });
    },

    datailsGo(o) {
      this.$router.push(o);
    }
  },
  created() {
    this.getcartoonsData();
  }
};
</script>
<style>
.fiction .van-nav-bar__title {
  font-size: 18px;
  color: #e4393c;
  font-weight: bolder;
}
</style>
<style lang="less" scoped>
.fiction-item {
  position: fixed;
  top: 46px;
  bottom: 50px;
  left: 0;
  right: 0;
  overflow-y: auto;
  .ping-box {
    width: 100%;
    float: left;
    overflow: hidden;
    overflow-y: auto;
    box-sizing: border-box;
  }
  .ping {
    background: white;
    padding: 3px;
    width: 100%;
    height: 150px;
    box-sizing: border-box;
    overflow: hidden;
    .phoImg {
      width: 30%;
      margin-right: 8px;
    }
    .pho {
      width: 110px;
      height: 140px;
      overflow: hidden;
      position: relative;
      box-shadow: 5px 8px 5px 1px #999999;
    }
    .infor {
      width: calc(~"100% - 30% - 9px");
      float: left;
      h3 {
        font-size: 22px;
        text-align: center;
        margin: 5px 0;
        color: #e4393c;
      }
      span {
        width: 50%;
        font-size: 16px;
        padding-top: 10px;
        text-align: center;
      }
      p {
        width: 100%;
        font-size: 16px;
        padding-top: 10px;
      }
    }
  }
}
</style>